"use client";

import { useState, useEffect } from "react";

export interface CookiePreferences {
  essential: boolean;
  analytics: boolean;
  marketing: boolean;
  functional: boolean;
}

export interface CookieConsentState {
  consentGiven: boolean; // Has the user made a choice?
  preferences: CookiePreferences;
}

const DEFAULT_PREFERENCES: CookiePreferences = {
  essential: true,
  analytics: true,
  marketing: true,
  functional: true,
};

export function useCookieConsent() {
  const [state, setState] = useState<CookieConsentState>({
    consentGiven: false,
    preferences: DEFAULT_PREFERENCES,
  });
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
    try {
      const stored = localStorage.getItem("cookie-consent-v2");
      if (stored) {
        setState(JSON.parse(stored));
      }
    } catch (e) {
      console.error("Failed to parse cookie consent", e);
    }
  }, []);

  const saveConsent = (preferences: CookiePreferences) => {
    const newState = {
      consentGiven: true,
      preferences: {
        ...preferences,
        essential: true, // Always forced
      },
    };
    setState(newState);
    localStorage.setItem("cookie-consent-v2", JSON.stringify(newState));
    
    // GTM Data Layer Update (if GTM is used)
    if (typeof window !== 'undefined') {
        window.dataLayer = window.dataLayer || [];
        window.dataLayer.push({
            event: 'cookie_consent_update',
            'analytics_storage': newState.preferences.analytics ? 'granted' : 'denied',
            'ad_storage': newState.preferences.marketing ? 'granted' : 'denied',
            'functionality_storage': newState.preferences.functional ? 'granted' : 'denied',
        });
    }
  };

  const acceptAll = () => {
    saveConsent({
      essential: true,
      analytics: true,
      marketing: true,
      functional: true,
    });
  };

  const rejectAll = () => {
    saveConsent({
      essential: true,
      analytics: false,
      marketing: false,
      functional: false,
    });
  };

  return {
    ...state,
    mounted,
    saveConsent,
    acceptAll,
    rejectAll,
  };
}

// Type for global dataLayer
declare global {
  interface Window {
    dataLayer: any[];
  }
}
