"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import Image from "next/image"
import { Container } from "@/components/ui/container"
import { Button } from "@/components/ui/button"
import { ThemeToggle } from "@/components/theme-toggle"
import { navLinks, services } from "@/lib/constants"
import { Sheet, SheetContent, SheetTrigger, SheetTitle } from "@/components/ui/sheet"
import { Menu, ChevronRight } from "lucide-react"
import { useState } from "react"
import { toSlug } from "@/lib/utils"

export function Navbar() {
  const pathname = usePathname()
  const [isOpen, setIsOpen] = useState(false)
  
  const scrollToSection = (e: React.MouseEvent<HTMLAnchorElement>, href: string) => {
    e.preventDefault()
    const element = document.querySelector(href)
    if (element) {
      element.scrollIntoView({ behavior: "smooth" })
      setIsOpen(false)
    }
  }

  // Group services structure for the nested menu
  const serviceGroups = [
    {
      label: "Engineering & Infrastructure",
      items: services.filter(s => ['Engineering', 'Infrastructure'].includes(s.subcategory || '')),
      description: "Build, scale, and modernize your core tech."
    },
    {
      label: "Operations & Automation",
      items: services.filter(s => s.subcategory === 'Operations'),
      description: "Autonomize workflows and support."
    },
    {
      label: "Data & Strategy",
      items: services.filter(s => ['Data & Insights', 'Strategy'].includes(s.subcategory || '')),
      description: "Turn data into decisions."
    },
    {
      label: "Growth & Marketing",
      items: services.filter(s => s.subcategory === 'Growth'),
      description: "Acquire and retain customers on autopilot."
    }
  ]

  return (
    <header className="sticky top-0 inset-x-0 w-full bg-background/80 backdrop-blur-lg border-b border-border z-50">
      <Container className="relative flex items-center justify-between h-16">
        <Link href="/" className="flex items-center gap-2">
          <div className="h-8 w-auto flex items-center justify-center">
            <Image
              src="/logo.png"
              alt="Arbor AI Studio Logo"
              width={0}
              height={0}
              sizes="100vw"
              className="h-full w-auto object-contain dark:brightness-0 dark:invert"
            />
          </div>
          <span className="text-xl font-semibold">Arbor AI Studio</span>
        </Link>

        {/* Desktop Navigation */}
        <nav className="hidden lg:block">
          <ul className="flex items-center justify-center gap-8">
            {navLinks.map((link) => {
              if (link.label === "Services") {
                return (
                  <li key={link.href} className="group relative">
                    {/* Main Nav Link */}
                    {pathname === "/" ? (
                      <a
                        href={link.href}
                        onClick={(e) => scrollToSection(e, link.href)}
                        className="text-sm hover:text-primary transition-colors cursor-pointer py-2 inline-block flex items-center gap-1"
                        data-gtm-nav={link.label}
                      >
                        {link.label}
                      </a>
                    ) : (
                      <Link
                        href={`/${link.href}`}
                        className="text-sm hover:text-primary transition-colors cursor-pointer py-2 inline-block flex items-center gap-1"
                        data-gtm-nav={link.label}
                      >
                        {link.label}
                      </Link>
                    )}
                    
                    {/* Level 1 Dropdown (Categories) */}
                    <div className="absolute left-1/2 -translate-x-1/2 top-full pt-2 hidden group-hover:block w-[280px]">
                      <div className="bg-background/95 backdrop-blur-xl border border-border/50 rounded-xl shadow-xl p-2 flex flex-col gap-1">
                        {serviceGroups.map((group) => (
                          <div key={group.label} className="group/item relative">
                            {/* Category Item */}
                            <div className="w-full px-4 py-3 text-sm rounded-lg hover:bg-muted/50 hover:text-primary transition-colors flex items-center justify-between cursor-pointer">
                              <span className="font-medium">{group.label}</span>
                              <ChevronRight className="w-4 h-4 text-muted-foreground group-hover/item:text-primary" />
                            </div>

                            {/* Level 2 Dropdown (Flyout - Specific Services) */}
                            <div className="absolute left-full top-0 pl-2 hidden group-hover/item:block w-[320px]">
                              <div className="bg-background/95 backdrop-blur-xl border border-border/50 rounded-xl shadow-xl p-2 flex flex-col gap-1">
                                <div className="px-4 py-2 text-xs text-muted-foreground border-b border-border/50 mb-1">
                                  {group.description}
                                </div>
                                {group.items.map((service) => (
                                  <Link
                                    key={service.title}
                                    href={`/services/${toSlug(service.title)}`}
                                    className="block px-4 py-2 text-sm rounded-lg hover:bg-muted/50 hover:text-primary transition-colors"
                                  >
                                    {service.title}
                                  </Link>
                                ))}
                              </div>
                            </div>
                          </div>
                        ))}
                      </div>
                    </div>
                  </li>
                )
              }

              return (
                <li key={link.href}>
                  {link.href.startsWith("#") ? (
                    pathname === "/" ? (
                      <a
                        href={link.href}
                        onClick={(e) => scrollToSection(e, link.href)}
                        className="text-sm hover:text-primary transition-colors cursor-pointer"
                        data-gtm-nav={link.label}
                      >
                        {link.label}
                      </a>
                    ) : (
                      <Link
                        href={`/${link.href}`}
                        className="text-sm hover:text-primary transition-colors cursor-pointer"
                        data-gtm-nav={link.label}
                      >
                        {link.label}
                      </Link>
                    )
                  ) : (
                    <Link
                      href={link.href}
                      className="text-sm hover:text-primary transition-colors cursor-pointer"
                      data-gtm-nav={link.label}
                    >
                      {link.label}
                    </Link>
                  )}
                </li>
              )
            })}
          </ul>
        </nav>

        <div className="flex items-center gap-4">
          <ThemeToggle />
          <Button asChild className="hidden lg:flex" id="btn-nav-get-started">
            {pathname === "/" ? (
              <a href="#contact" onClick={(e) => scrollToSection(e, "#contact")}>
                Get Started
              </a>
            ) : (
              <Link href="/#contact">
                Get Started
              </Link>
            )}
          </Button>

          <Sheet open={isOpen} onOpenChange={setIsOpen}>
            <SheetTrigger asChild>
              <Button variant="ghost" size="icon" className="lg:hidden" aria-controls="mobile-menu">
                <Menu className="h-6 w-6" />
                <span className="sr-only">Toggle menu</span>
              </Button>
            </SheetTrigger>
            <SheetContent side="right" id="mobile-menu">
              <SheetTitle className="sr-only">Navigation Menu</SheetTitle>
              <div className="flex flex-col gap-6 mt-8">
                <Link href="/" className="flex items-center gap-2" onClick={() => setIsOpen(false)}>
                  <div className="h-8 w-auto flex items-center justify-center">
                    <Image
                      src="/logo.png"
                      alt="Arbor AI Studio Logo"
                      width={0}
                      height={0}
                      sizes="100vw"
                      className="h-full w-auto object-contain dark:brightness-0 dark:invert"
                    />
                  </div>
                  <span className="text-xl font-semibold">Arbor AI Studio</span>
                </Link>
                <nav className="flex flex-col gap-4">
                  {navLinks.map((link) => (
                    <div key={link.href}>
                      {link.href.startsWith("#") ? (
                        pathname === "/" ? (
                          <a
                            href={link.href}
                            onClick={(e) => scrollToSection(e, link.href)}
                                                        className="text-lg font-medium text-foreground hover:text-primary transition-colors"
                            data-gtm-nav={link.label}
                          >
                            {link.label}
                          </a>
                        ) : (
                          <Link
                            href={`/${link.href}`}
                                                        className="text-lg font-medium text-foreground hover:text-primary transition-colors"
                            onClick={() => setIsOpen(false)}
                            data-gtm-nav={link.label}
                          >
                            {link.label}
                          </Link>
                        )
                      ) : (
                        <Link
                          href={link.href}
                                                      className="text-lg font-medium text-foreground hover:text-primary transition-colors"
                          onClick={() => setIsOpen(false)}
                          data-gtm-nav={link.label}
                        >
                          {link.label}
                        </Link>
                      )}
                    </div>
                  ))}
                  <Button asChild className="mt-4 w-full" id="btn-nav-get-started-mobile">
                    {pathname === "/" ? (
                      <a href="#contact" onClick={(e) => scrollToSection(e, "#contact")}>
                        Get Started
                      </a>
                    ) : (
                      <Link href="/#contact" onClick={() => setIsOpen(false)}>
                        Get Started
                      </Link>
                    )}
                  </Button>
                </nav>
              </div>
            </SheetContent>
          </Sheet>
        </div>
      </Container>
    </header>
  )
}